.item {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.icon {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 2px;
  right: 10px;
}

.button {
  border: 1px solid black;
  background-color: #fff;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 5px;
}
